@import url("https://fonts.googleapis.com/icon?family=Material+Icons");

body {
  height: 100%;
}
.login-main {
  position: fixed;
  padding: 0;
  margin: 0;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;
  background-image: url("./assets/LoginBackround.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
.login-main-container {
  color: white;
  top: 20%;
  position: relative; /* or absolute */
}

.main-container {
  height: 100%;
  max-height: fit-content;
  padding-top: 15px;
}
